// 
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
// 
function exportToExcel (id, name) {
  var xlsxParam = { raw: true }
  var wb = XLSX.utils.table_to_book(document.querySelector(id), xlsxParam)
  var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
  try {
    FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), name + '.xlsx')
  } catch (e) {
    if (typeof console !== 'undefined') {
    }
  }
  return wbout
}

//  true
function isNullAndEmpty (param) {
  if (param instanceof Array) { // 
    if (param.length > 0) {
      return false
    } else {
      return true
    }
  } else if (typeof param === 'string') { // 
    if (param === undefined || param === null || param.trim() === '') {
      return true
    } else {
      return false
    }
  } else if (Object.prototype.toString.call(param) === '[object Object]') { // 
    for (var key in param) {
      return false
    }
    return true
  }
}

const funJS = { // 
  throttle (fn, delay) { // 
    var lastTime = 0 // 
    return function () {
      let nowTime = Date.now() // 
      if (nowTime - lastTime > delay) {
        fn().call(this) // this
        lastTime = nowTime // 
      }
    }
  },
  debounce (fn, delay) { // 
    var timer = null // 
    return function () {
      clearTimeout(timer) // 
      timer = setTimeout(function () {
        fn.apply(this)
      }, delay)
    }
  }
}

const timeJS = { // 
  formatTime (param, format) {
    let d = param === 0 ? new Date() : new Date(param)
    let year = d.getFullYear()
    let month = (d.getMonth() + 1 < 10) ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1)
    let day = (d.getDate() < 10) ? '0' + d.getDate() : d.getDate()
    let hour = (d.getHours() < 10) ? '0' + d.getHours() : d.getHours()
    let minute = (d.getMinutes() < 10) ? '0' + d.getMinutes() : d.getMinutes()
    let second = (d.getSeconds() < 10) ? '0' + d.getSeconds() : d.getSeconds()
    switch (format) {
      case 'timestamp' :
        return d.valueOf()
      case 'yyyy':
        return year
      case 'yyyy-MM':
        return year + '-' + month
      case 'yyyyMM':
        return year + '' + month + ''
      case 'MM-dd':
        return month + '-' + day
      case 'MMdd':
        return month + '' + day + ''
      case 'yyyy-MM-dd':
        return year + '-' + month + '-' + day
      case 'yyyyMMdd':
        return year + '' + month + '' + day + ''
      case 'yyyy-MM-dd hh:mm:ss':
        return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
      case 'yyyyMMdd hhmmss':
        return year + '-' + month + '-' + day + ' ' + hour + '' + minute + '' + second + ''
      default:
        return d
    }
  },
  getNowTime (format) { // 
    return this.formatTime(0, format)
  },
  timeToStamp (timestamp, format) { // 
    return this.formatTime(timestamp, format)
  },
  compareTime (startTime, endTime) { // 
    if (new Date(endTime).valueOf() - new Date(startTime).valueOf() >= 0) {
      return true
    } else {
      return false
    }
  },
  countdown (time) { // 
    let nowtime = new Date()
    let endtime = new Date(time)
    let lefttime = endtime.getTime() - nowtime.getTime()
    if (lefttime > 0) {
      let leftd = Math.floor(lefttime / (1000 * 60 * 60 * 24))
      let lefth = Math.floor(lefttime / (1000 * 60 * 60) % 24)
      let leftm = Math.floor(lefttime / (1000 * 60) % 60)
      let lefts = Math.floor(lefttime / 1000 % 60)
      if (leftd > 0) {
        return leftd + ' ' + lefth + ':' + leftm + ':' + lefts
      } else {
        return lefth + ':' + leftm + ':' + lefts
      }
    } else {
      return false
    }
  }
}

const arrayJS = { // 
  unqiueArr (arr) { // 
    let [...arr1] = new Set(arr)
    return arr1
  },
  tileArr (arr) { // 
    return arr.reduce((acc, cur) =>
      acc.concat(Array.isArray(cur) ? this.tileArr(cur) : cur), [])
  },
  arrOperation (arr1, arr2, mode) { // 
    let arr11 = new Set(arr1)
    let arr22 = new Set(arr2)
    switch (mode) {
      case 1: // 
        return arr1.filter(x => arr22.has(x))
      case 2: // 
        return arr1.filter(x => !arr22.has(x))
      case 3: // 
        return [...arr1.filter(x => !arr22.has(x)), ...arr2.filter(x => !arr11.has(x))]
      case 4: // 
        return Array.from(new Set([...arr1, ...arr2]))
    }
  }
}

const objectJS = {
  cloneObject (target, source) { // 
    var names = Object.getOwnPropertyNames(source) // 
    for (var i = 0; i < names.length; i++) {
      var desc = Object.getOwnPropertyDescriptor(source, names[i])
      if (typeof (desc.value) === 'object' && desc.value !== null) {
        var obj
        if (Array.isArray(desc.value)) {
          obj = []
        } else {
          obj = {}
        }
        Object.defineProperty(target, names[i], {
          configurable: desc.configurable, // 
          enumerable: desc.enumerable, // 
          value: obj, // 
          writable: desc.writable // 
        })
        this.cloneObject(obj, desc.value)
      } else {
        Object.defineProperty(target, names[i], desc)
      }
    }
  },
  clearValue (obj) { // value(string''/Array[]/Number0/Booleantrue)
    for (let key in obj) {
      if (typeof obj[key] === 'string') {
        obj[key] = ''
      } else if (obj[key] instanceof Array) {
        obj[key] = []
      } else if (typeof obj[key] === 'number') {
        obj[key] = 0
      } else if (typeof obj[key] === 'boolean') {
        obj[key] = true
      }
    }
  }
}
function sortLikeWin (name1, name2) {
  const regexPunc = /[\s!#$%&(),.;@[\]^_`{}~+=]/
  const regexNum = /[0-9]/
  const regexEng = /[A-Za-z]/
  const regexCh = /[\u4E00-\u9FFF]/
  //   >  >  > 
  // 
  let compareValue = false
  const minLength = Math.min(name1.length, name2.length)
  let i = 0
  do {
    const aIndex = name1.charAt(i)
    const bIndex = name2.charAt(i)
    const nameFirstType = [aIndex, bIndex].map((item) => {
      if (item.match(regexPunc)) {
        return 0
      }
      if (item.match(regexNum)) {
        return 1
      }
      if (item.match(regexEng)) {
        return 2
      }
      if (item.match(regexCh)) {
        return 3
      }
      return -1
    })
    // 
    if (aIndex !== bIndex) {
      if (nameFirstType[0] !== nameFirstType[1]) {
        compareValue = nameFirstType[0] - nameFirstType[1]
        break
      } else {
        // 
        compareValue = aIndex.localeCompare(bIndex, 'zh')
        break
      }
    }
    if (i === minLength) {
      compareValue = name1.localeCompare(name2, 'zh')
      break
    }
    i += 1
  } while (i <= minLength)
  return compareValue
}
export {
  exportToExcel,
  isNullAndEmpty,
  funJS, timeJS, arrayJS, objectJS,
  sortLikeWin
}



